.move-set-container {
  max-height: 213px;
  max-width: 300px;
  overflow-y: scroll;
  overflow-x: hidden;
  font-size: 13px;
  margin: auto;
}

.move-container {
  margin: auto;
  min-height: 25px;
  max-height: 25px;
  min-width: 170px;
  max-width: 170px;
  text-align: left;
  justify-content: space-evenly;
  overflow: hidden;
  margin-top: 4px;
  padding: 0px 2rem 0px 2rem;
}

.flex {
  display: flex;
}

.margin-auto {
  margin: auto;
}

.move-category-img {
  border-radius: 25px;
  min-width: 20px;
  max-width: 20px;
  cursor: auto;
  display: inline;
  vertical-align: bottom;
}
