.filter-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.filter-item {
  text-align: center;
  flex: 1 1 150px; /*  Stretching: */
  flex: 0 1 150px; /*  No stretching: */
  margin: 5px 5px 20px 5px;
}

input {
  border-radius: 8px;
  border: 1px solid #afafaf;
}
input {
  border: 1px solid #808080;
}

select {
  border-radius: 8px;
  border: 1px solid #afafaf;
}

select:focus {
  border: 1px solid #808080;
}

.filter-option {
  min-width: 150px;
}
