.icon-container {
  overflow-y: scroll;
  max-height: 57vh;
}

ul {
  margin: auto;
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

li {
  color: white;
  padding: 0px 6px 0px 6px;
  text-align: center;
}
