body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scrollbar-color: #d1d1d1d1 rgb(240, 240, 240);
  scrollbar-width: thin;
  cursor: default;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background-color: rgb(240, 240, 240);
  border-radius: 8px;
}

::-webkit-scrollbar-thumb {
  width: 2px;
  height: 80px;
  background-color: #d1d1d1d1;
  border: 1px solid rgb(210, 210, 210);
  border-radius: 8px;
}

h2 {
  margin: 1rem;
}

p {
  text-align: center;
}

div > p {
  font-weight: bold;
}

img {
  width: 100%;
}

.container {
  margin: auto;
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.container {
  margin: auto;
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-flow: wrap;
  justify-content: space-evenly;
}

.container-item {
  margin-top: 1rem;
  width: 900px;
}

.team-container {
  padding: 1rem 0rem 1rem 0rem;
  margin: 1rem 0rem;
  display: flex;
  overflow-y: scroll;
  min-height: 80vh;
  max-height: 80vh;
  justify-content: center;
}

.box-shadow {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 25px 50px -25px;
}

.margin-bottom {
  border-radius: 25px;
}

.type-container {
  display: flex;
  text-align: center;
  justify-content: space-evenly;
  vertical-align: top;
}

.type-item {
  width: 80px;
}

.no-margin {
  margin: 4px 4px 4px 4px;
  padding: 0;
}

.no-margin-bottom {
  margin-bottom: 0;
}

.ability {
  margin: 0;
  padding-right: 4px;
  padding-left: 4px;
}

.icon-container {
  margin: auto;
  place-items: center;
  padding: 50px;
}
